import { customElement, property } from 'lit/decorators.js';
import { css, CSSResultGroup, html, LitElement, TemplateResult } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import { styleMap } from 'lit/directives/style-map.js';
import { getColorVar } from '../../utils';

@customElement('zwc-avatar')
export class ZwcAvatar extends LitElement {
  @property({ type: String, attribute: 'background-color', reflect: true })
  backgroundColor = '';

  @property({ type: String })
  color = '';

  @property({ type: String })
  size = 'md';

  @property({ type: Boolean })
  outline = false;

  static override styles?: CSSResultGroup | undefined = [
    css`
      *,
      *::before,
      *::after {
        box-sizing: border-box;
      }

      .avatar {
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-family: var(--zwc-avatar-font, var(--f-sans-medium));
        font-size: var(--font-size-base-xs);
        color: var(--zwc-avatar-color, var(--c-white));
        background-color: var(--zwc-avatar-background, var(--c-zurichblue));
        position: relative;
        vertical-align: top;
      }

      ::slotted(img) {
        position: absolute;
        height: inherit;
        border-radius: 50%;
      }

      .avatar.sm,
      .sm ::slotted(img) {
        min-width: 40px;
        height: 40px;
        font-size: var(--font-size-base-xs);
      }

      .avatar.md,
      .md ::slotted(img) {
        min-width: 56px;
        height: 56px;
        font-size: var(--font-size-base);
      }

      .avatar.lg,
      .lg ::slotted(img) {
        min-width: 80px;
        height: 80px;
        font-size: 28px;
        font-family: var(--zwc-avatar-font, var(--f-sans-regular));
      }
    `,
  ];

  protected override render(): TemplateResult {
    const classes = {
      avatar: true,
      [`${this.size}`]: this.size,
      'background-color': `${this.backgroundColor}`,
    };

    const styles = {
      backgroundColor: this.backgroundColor
        ? getColorVar(this.backgroundColor)
        : null,
      border: this.outline ? `1.5px solid ${getColorVar(this.color)}` : null,
      color: this.color ? `${getColorVar(this.color)}` : null,
    };

    return html`
      <div class="${classMap(classes)}" style="${styleMap(styles)}">
        <slot name="icon"></slot>
        <slot name="image"></slot>
        <slot></slot>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-avatar': ZwcAvatar;
  }
}
